import React, { Component } from 'react'
import axios from "axios"
import './compo.css'
import { Link } from 'react-router-dom'

export class ClassDeleteEmployee extends Component {

    constructor(props) {
        super(props)

        this.state = {
            id: 19
        }
    }

    changeHandler = (e) => {
        this.setState({ id: e.target.value })
    }

    submitHandler = (e) => {
        e.preventDefault()
        console.log(this.state)
        axios.delete(`https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/${this.state.id}`)
            .then(response => {
                console.log(response)
                this.setState({ candidate: response.data })
            })
            .catch(error => {
                console.log(error)
            })

        alert("CAndidate successfully deleted")
    }

    render() {
        return (
            <div>
                <form onSubmit={this.submitHandler}>
                    <div>
                        <label>Candidate ID : </label><input type="text" name="id" value={this.id} onChange={this.changeHandler}></input>
                    </div>
                    <button type="submit" className="w3btn">DELETE</button>
                    <Link to="/AdminHandler"><button className="w3btn">BACK</button></Link>
                </form>


            </div>
        )
    }
}

export default ClassDeleteEmployee
