import React from 'react';
import './App.css';
import ClassHome from "./components/ClassHome"
import Candidate from "./components/Candidates/Candidate"
import ClassIndividual from "./components/ClassIndividual"
import ClassAddCandidate from "./components/ClassAddCandidate"
import ClassUpdateCandidate from "./components/ClassUpdateCandidate"
import ClassDeleteEmployee from "./components/ClassDeleteEmployee"
import AdminAuthenticate from "./components/AdminAuthenticate"
import AdminHandler from "./components/AdminHandler"
import OnLoad from "./components/OnLoad"
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { Link } from 'react-router-dom'

function App() {
  return (
    <Router>
      <div className="App divhome">
        <h1>Hacker Polls</h1>
        <Switch>
          <Route path="/" exact component={OnLoad} />

          <Route path="/Home" component={ClassHome} />
          <Route exact path="/Candidate/:candidateId" component={Candidate} />
          <Route path="/Individual" component={ClassIndividual} />
          <Route path="/AdminAuthenticate" component={AdminAuthenticate} />
          <Route path="/AdminHandler" component={AdminHandler} />
          <Route path="/adminAdd" component={ClassAddCandidate} />
          <Route path="/adminupdate" component={ClassUpdateCandidate} />
          <Route path="/admindelete" component={ClassDeleteEmployee} />
        </Switch>


      </div>
    </Router>
  );
}

export default App;
