import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './compo.css'

export class OnLoad extends Component {
    render() {
        return (
            <div className="divload">
                <Link to="/Home"><button className="w3btn">ENTER AS USER</button></Link>
                <Link to="/AdminHandler"><button className="w3btn">ENTER AS ADMIN</button></Link>

            </div>
        )
    }
}

export default OnLoad
