import React, { Component } from 'react'
import axios from "axios"
import { Link } from 'react-router-dom'
import './compo.css'


class ClassAddCandidate extends Component {

    constructor(props) {
        super(props)

        this.state = {
            Candidate_Name: "",
            Gender: "",
            Challenges_Solved: 0,
            Rating_DS: 0,
            Rating_Algorithms: 0,
            Rating_CPP: 0,
            Rating_Python: 0,
            Rating_Java: 0,
            Votes: 0
        }
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }

    submitHandler = (e) => {
        e.preventDefault()
        console.log(this.state)
        axios.post("https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/", this.state)
            .then(response => {
                console.log(response)
            })
            .catch(error => {
                console.log(error)
            })

        alert("Candidate successfully added")
    }


    render() {
        const { Candidate_Name, Gender, Challenges_Solved, Rating_DS, Rating_Algorithms, Rating_CPP, Rating_Python, Rating_Java, Votes } = this.state
        return (
            <div>
                <form onSubmit={this.submitHandler}>
                    <div>
                        <label>Candidate Name : </label><input type="text" name="Candidate_Name" value={Candidate_Name} onChange={this.changeHandler} required></input>
                    </div>
                    <div>
                        <label>Gender : </label><input type="text" name="Gender" value={Gender} onChange={this.changeHandler} required></input>
                    </div>
                    <div>
                        <label>Challenges Solved : </label><input type="number" name="Challenges_Solved" value={Challenges_Solved} onChange={this.changeHandler} min="0" required></input>
                    </div>
                    <div>
                        <label>Expertise in DS : </label><input type="number" name="Rating_DS" value={Rating_DS} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in Algorithm : </label><input type="number" name="Rating_Algorithms" value={Rating_Algorithms} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in CPP : </label><input type="number" name="Rating_CPP" value={Rating_CPP} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in Python : </label><input type="number" name="Rating_Python" value={Rating_Python} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in Java : </label><input type="number" name="Rating_Java" value={Rating_Java} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Votes : </label><input type="number" name="Votes" value={Votes} onChange={this.changeHandler} min="0" max="5"></input>
                    </div>
                    <button type="submit" className="w3btn">ADD CANDIDATE</button>
                    <Link to="/AdminHandler"><button className="w3btn">BACK</button></Link>
                </form>

            </div>
        )
    }
}

export default ClassAddCandidate
