import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import AdminHandler from "./AdminHandler"
import './compo.css'

class AdminAuthenticate extends Component {
    constructor(props) {
        super(props)

        this.state = {
            adminCode: "HackerPollsAdmin",
            userValue: "",
        }
    }

    handleChange = event => {
        this.setState({
            userValue: event.target.value
        })
    }

    submitHandler = event => {
        console.log(this.state.userValue)

        if (this.state.userValue === this.state.adminCode) {
            console.log("Equal");

        }
        else {
            console.log("Not equal");
            alert("CODE ERROR")

        }
    }



    render() {
        return (
            <div className="adminload">
                <div>
                    --YET TO COMPLETE --
                    <label>Enter Admin Code : </label>
                    <input type="text" name="userValue" value={this.state.userValue} onChange={this.handleChange}></input>
                    <button onClick={this.submitHandler}>AUTHENTICATE</button>
                </div>
            </div>
        )
    }
}

export default AdminAuthenticate
