import React, { Component } from 'react'
import axios from "axios";
import { SemipolarLoading } from "react-loadingg";
import './compo.css'
import { Link, withRouter } from "react-router-dom"

class ClassHome extends Component {

    constructor(props) {
        super(props)

        this.state = {
            candidates: [],
            selectedCandidateId: ''
        }
        this.loading = true;
        this.clickHandler = this.clickHandler.bind(this);

    }

    clickHandler(id) {
        this.state.selectedCandidateId = id

        console.log(id)
    }

    componentDidMount() {
        axios.get("https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates")
            .then(response => {
                console.log(response)
                this.loading = false
                this.setState({ candidates: response.data })
            })
            .catch(error => {
                this.loading = false
                console.log(error)
            })

    }


    render() {
        const { candidates } = this.state
        if (this.loading) {
            return (
                <div>
                    <SemipolarLoading />
                </div>
            )
        }
        else {
            return (
                <div>
                    <ul>
                        {candidates.map(candidate => (
                            <li key={candidate.ID}>
                                <Link to={`/Candidate/${candidate.ID}`}>
                                    <button className="w3btn" onClick={() => this.clickHandler(candidate.ID)}>{candidate.Candidate_Name}</button>
                                </Link>
                            </li>
                        ))}
                    </ul>
                </div>
            )
        }
    }
}

export default ClassHome
