import React, { Component } from 'react'
import { SemipolarLoading } from "react-loadingg";
import { Link } from 'react-router-dom'
import axios from "axios";
import '../compo.css'

class Candidate extends Component {

    constructor(props) {
        super(props)

        this.state = {
            candidates: [],
            selectedCandidateId: '',
            individual: {
                Candidate_Name: "",
                Gender: "",
                Challenges_Solved: 0,
                Rating_DS: 0,
                Rating_Algorithms: 0,
                Rating_CPP: 0,
                Rating_Python: 0,
                Rating_Java: 0,
                Votes: 0
            }
        }
        this.loading = true;
        this.clickHandler = this.clickHandler.bind(this);

    }

    clickHandler(id) {
        //console.log(id)
        // console.log(this.state.candidates.Candidate_Name)
        // console.log(this.state.candidates.Votes)

        this.state.candidates.Votes = this.state.candidates.Votes + 1
        console.log(this.state.individual)

        this.state.individual.Candidate_Name = this.state.candidates.Candidate_Name;
        this.state.individual.Gender = this.state.candidates.Gender;
        this.state.individual.Challenges_Solved = this.state.candidates.Challenges_Solved;
        this.state.individual.Rating_DS = this.state.candidates.Rating_DS;
        this.state.individual.Rating_Algorithms = this.state.candidates.Rating_Algorithms;
        this.state.individual.Rating_CPP = this.state.candidates.Rating_CPP;
        this.state.individual.Rating_Python = this.state.candidates.Rating_Python;
        this.state.individual.Rating_Java = this.state.candidates.Rating_Java;
        this.state.individual.Votes = this.state.candidates.Votes;


        //console.log(`https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/${this.state.candidates.ID}`)
        axios.put(`https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/${this.state.candidates.ID}`, this.state.individual)
            .then(res => { console.log(res) })
            .catch(err => { console.log(err) })

        alert("Thanks for Voting");
    }

    componentDidMount() {
        //console.log("Window location path =" + window.location.pathname);
        //console.log("https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/" + (window.location.pathname))
        console.log(window.location.hash)
        console.log((window.location.hash).split("/")[2])
        console.log(window.location.pathname)
        axios.get("https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/" + (window.location.hash).split("/")[2])
            .then(response => {
                console.log(response)
                this.loading = false
                this.setState({ candidates: response.data })
            })
            .catch(error => {
                this.loading = false
                console.log(error)
            })

    }

    render() {
        if (this.loading) {
            return (
                <div>
                    <SemipolarLoading />
                </div>
            )
        }
        else {
            return (
                <div>
                    <p>
                        <strong>Candidate ID: </strong>
                        {this.state.candidates.ID}
                    </p>
                    <p>
                        <strong>Candidate Name: </strong>
                        {this.state.candidates.Candidate_Name}
                    </p>
                    <p>
                        <strong>Challenges_Solved: </strong>
                        {this.state.candidates.Challenges_Solved}
                    </p>
                    <button type="submit" className="w3btn" onClick={() => this.clickHandler(this.state.candidates.ID)}> VOTE </button>
                    <Link to="/Home"><button className="w3btn">BACK</button></Link>
                </div>
            )
        }
    }
}

export default Candidate