import React, { Component } from 'react'
import axios from "axios"

class ClassIndividual extends Component {

    constructor(props) {
        super(props)

        this.state = {
            candidate: {}, id: 1,
            Candidate_Name: "",
            Gender: "",
            Challenges_Solved: 0,
            Rating_DS: 0,
            Rating_Algorithms: 0,
            Rating_CPP: 0,
            Rating_Python: 0,
            Rating_Java: 0,
            Votes: 0
        }


    }

    componentDidMount() {
        axios.get("https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/1")
            .then(response => {
                console.log(response)
                this.setState({ candidate: response.data })
            })
            .catch(error => {
                console.log(error)
            })
    }

    render() {

        return (
            <div>
                <form >
                    <p>Name of the Candidate : {this.state.candidate.Candidate_Name}</p>
                    <p>Number Of Challenges solved : {this.state.candidate.Challenges_Solved}</p>
                    <p>Gender : {this.state.candidate.Gender}</p>
                    <p>Expertise in Data Structures :{this.state.candidate.Rating_DS}</p>
                    <p>Expertise in Algorithms :{this.state.candidate.Rating_Algorithms}</p>
                    <p>Expertise in Java :{this.state.candidate.Rating_Java}</p>
                    <p>Expertise in C++ :{this.state.candidate.Rating_CPP}</p>
                    <p>Number Of Votes :{this.state.candidate.Votes}</p>

                    <button type="submit" className="w3btn"> VOTE </button>
                </form>

            </div>
        )
    }
}

export default ClassIndividual
