import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './compo.css'

export class AdminHandler extends Component {
    render() {
        return (
            <div className="adminload">

                <Link to="/adminAdd"><button className="w3btn">INSERT CANDIDATE</button></Link>
                <Link to="/adminupdate"><button className="w3btn">UPDATE CANDIDATE</button></Link>
                <Link to="/admindelete"><button className="w3btn">DELETE CANDIDATE</button></Link>
            </div>

        )
    }
}

export default AdminHandler
