import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import axios from "axios"

class ClassUpdateCandidate extends Component {

    constructor(props) {
        super(props)

        this.state = {
            ID: "",
            Candidate_Name: "",
            Gender: "",
            Challenges_Solved: 0,
            Rating_DS: 0,
            Rating_Algorithms: 0,
            Rating_CPP: 0,
            Rating_Python: 0,
            Rating_Java: 0,
            Votes: 0,
            individual: {
                Candidate_Name: "",
                Gender: "",
                Challenges_Solved: 0,
                Rating_DS: 0,
                Rating_Algorithms: 0,
                Rating_CPP: 0,
                Rating_Python: 0,
                Rating_Java: 0,
                Votes: 0
            }
        }
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }

    submitHandler(id) {
        this.state.individual.Candidate_Name = this.state.Candidate_Name
        this.state.individual.Gender = this.state.Gender;
        this.state.individual.Challenges_Solved = this.state.Challenges_Solved;
        this.state.individual.Rating_DS = this.state.Rating_DS;
        this.state.individual.Rating_Algorithms = this.state.Rating_Algorithms;
        this.state.individual.Rating_CPP = this.state.Rating_CPP;
        this.state.individual.Rating_Python = this.state.Rating_Python;
        this.state.individual.Rating_Java = this.state.Rating_Java;
        this.state.individual.Votes = this.state.Votes;

        //console.log(this.state.individual);
        //console.log(`http://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/${this.state.ID}`)

        axios.put(`https://hackercandidatesapi20200808014029.azurewebsites.net/api/candidates/${this.state.ID}`, this.state.individual)
            .then(res => { console.log(res) })
            .catch(err => { console.log(err) })
        alert("CAndidate successfully Updated")
    }


    render() {
        const { ID, Candidate_Name, Gender, Challenges_Solved, Rating_DS, Rating_Algorithms, Rating_CPP, Rating_Python, Rating_Java, Votes } = this.state

        return (
            <div>
                <div >
                    <div>
                        <label>Candidate ID : </label><input type="text" name="ID" value={ID} onChange={this.changeHandler} required></input>
                    </div>
                    <div>
                        <label>Candidate Name : </label><input type="text" name="Candidate_Name" value={Candidate_Name} onChange={this.changeHandler} required></input>
                    </div>
                    <div>
                        <label>Gender : </label><input type="text" name="Gender" value={Gender} onChange={this.changeHandler} required></input>
                    </div>
                    <div>
                        <label>Challenges Solved : </label><input type="number" name="Challenges_Solved" value={Challenges_Solved} onChange={this.changeHandler} min="0"></input>
                    </div>
                    <div>
                        <label>Expertise in DS : </label> <input type="number" name="Rating_DS" value={Rating_DS} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in Algo : </label><input type="number" name="Rating_Algorithms" value={Rating_Algorithms} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in CPP : </label><input type="number" name="Rating_CPP" value={Rating_CPP} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in Python : </label><input type="number" name="Rating_Python" value={Rating_Python} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Expertise in Java : </label> <input type="number" name="Rating_Java" value={Rating_Java} onChange={this.changeHandler} min="1" max="5" required></input>
                    </div>
                    <div>
                        <label>Votes : </label><input type="number" name="Votes" value={Votes} onChange={this.changeHandler} min="0" max="5" required></input>
                    </div>
                    <button type="submit" className="w3btn" onClick={() => this.submitHandler(this.state.ID)}>UPDATE CANDIDATE</button>
                    <Link to="/AdminHandler"><button className="w3btn">BACK</button></Link>
                </div>

            </div>
        )
    }
}

export default ClassUpdateCandidate
